<!--test.vue-->
<template>
    <div class="fhv-test" @click="handleClick">{{ num }}</div>
</template>

<script>
    /*
    *  name 
    *  name  name  XrTest
    * <fhv-test></fhv-test> Element name  ElButton
    * <el-button></el-button>
    * */
    export default {
        name: 'FhvTest', // <fhv-test></fhv-test>
        data() {
            return {
                num: 0
            }
        },
        methods: {
            handleClick() {
                this.num++
            }
        }
    }
</script>

<style lang="scss" scoped>
    .fhv-test {
        width: 100px;
        height: 100px;
        line-height: 100px;
        border-radius: 50%;
        font-size: 30px;
        text-align: center;
        background: #24292e;
        color: white;
    }
</style>
