/*
* 
* 1import
* 2
* */
import FhvTest from './test'
// 
const components = [
    FhvTest
]

// installvue
const install = Vue => {
    // 
    if (install.installed) return
    install.installed = true
    // 
    components.map(component => Vue.component(component.name, component))
}

// vuevue.js
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}

export default {
    //  install Vue.use() 
    install,
    ...components
}
